video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero-container {
  /* background: url("../img/img-home.jpg") center center/cover no-repeat; */
  background: linear-gradient(90deg, rgb(255, 60, 0) 0%, rgb(255, 145, 0) 60%);
  /* background-image: linear-gradient(
      90deg,
      rgba(255, 60, 0, 1),
      rgba(255, 145, 0, 1)
    ),
    url("../img/img-home.jpg"); */
  height: 100vh;
  max-height: 45rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
  object-fit: contain;
  position: relative;
  /* z-index: 9; */
  overflow: hidden;
}

.hero-illustration {
  position: absolute;
  top: 0;
  left: 40%;
  z-index: 4;
}
.hero-illustration img {
  height: 35rem;
}
.hero-content {
  z-index: 5;
}
.hero-content > h1 {
  color: rgb(255, 255, 255);
  font-size: 80px;
  margin-top: -100px;
}

.hero-content > p {
  margin-top: 8px;
  color: rgb(255, 255, 255);
  font-size: 32px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.hero-btns {
  margin-top: 32px;
  color: #fff;
  display: flex;
}

.hero-btns Button {
  margin: 10px;
}

.btn-outline:hover {
  background-color: #fff !important;
  color: #ff9100 !important;
}
@media screen and (max-width: 960px) {
  .hero-content > h1 {
    font-size: 60px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-content > h1 {
    font-size: 40px;
    margin-top: -100px;
  }

  .hero-content > p {
    font-size: 20px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
