.card-container {
  background-color: rgb(72, 72, 72);
  padding-bottom: 3rem;
}
.card-container h1 {
  margin: 0;
  padding: 3rem;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}
.card-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.card-row:not(:first-of-type) {
  padding-top: 2rem;
}
.card-content {
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.reverse {
  flex-direction: row-reverse;
}
.card-box {
  background-color: black;
  border-radius: 2rem;
  width: 40rem;
  height: max-content;
  /* margin: 2rem; */
  transition: all 0.2s ease-in-out;
}
.card-box:hover {
  transform: translateY(-5px);
  box-shadow: 4px 4px 4px 2px rgba(0, 0, 0, 0.75);
  transition: all 0.2s ease-in-out;
}
.card-img img {
  height: 10rem;
  max-width: 19rem;
  transition: all 0.2s ease-in-out;
}
.card-img img:hover {
  transform: rotateZ(7deg);
  transition: all 0.2s ease-in-out;
}
.reverse .card-img img:hover {
  transform: rotateZ(-7deg);
  transition: all 0.2s ease-in-out;
}
.card-text {
  color: #ff7b00;
  text-align: center;
}
.card-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card-btn Button {
  margin-bottom: 1rem;
}

@media screen and (max-width: 1300px) {
  .card-content {
    flex-direction: column;
  }
  .card-box {
    width: 47vw;
    /* height: 25rem; */
  }

  .card-text {
    height: 4rem;
  }
  .card-btn {
    flex-direction: row;
    justify-content: space-around;
  }
  .card-btn Button {
    margin-bottom: 0rem;
  }
}

@media screen and (max-width: 870px) {
  .card-btn {
    flex-direction: column;
  }
  .card-btn Button:first-of-type {
    margin-bottom: 0.8rem;
  }
  .card-img img {
    max-width: 15rem;
  }
  .card-text {
    font-size: 0.8rem;
  }
  .card-text {
    height: 5rem;
  }
}
@media screen and (max-width: 500px) {
  body {
    width: 130vw;
  }
  .card-row {
    flex-direction: column;
  }
  .card-row:not(:first-of-type) {
    padding-top: 0rem;
  }
  .card-box {
    margin: 0.7rem;
  }
  .card-container h1 {
    padding-bottom: 1rem;
  }

  .card-box {
    width: 85vw;
  }
}
