.navbar {
  /* background: linear-gradient(90deg, rgb(255, 60, 0) 0%, rgb(255, 145, 0) 100%); */
  background: linear-gradient(90deg, rgb(255, 60, 0) 0%, rgb(255, 145, 0) 60%);
  /* background-color: #00000; */
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

a {
  cursor: pointer;
}
.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.nav-logo ion-icon {
  margin-right: 1rem;
  font-size: 2rem;
  vertical-align: middle;
}

.nav-menu {
  /* display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px; */
  display: flex;
  align-content: center;
  list-style: none;
  text-align: center;
  width: 55vw;
  justify-content: flex-end;
  margin-right: 2rem;
}

.nav-item {
  /* height: 80px; */
}

.btn-nav {
  text-decoration: none;
}

.nav-links {
  color: #fff;
  display: flex;
  /* align-self: center; */
  visibility: visible;
  vertical-align: middle;
  text-decoration: none;
  padding: 0.47rem 1rem;
  height: 100%;
  border-bottom: 4px solid transparent;
  transition: all 0.2s ease-out;
}

.nav-links:hover,
.nav-links:active {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}
ion-icon {
  color: #fff;
  font-size: 2rem;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.MuiButton-outlined:hover {
  background-color: #fff !important;
  color: #ff9100 !important;
}
.MuiButton-outlined {
  color: #fff !important;

  border: 2px solid #fff !important;
  height: min-content !important;
}
