@media screen and (max-width: 1000px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* justify-items: center; */
    width: 50%;
    /* height: 120vh; */
    max-height: max-content;
    position: absolute;
    top: 50px;
    left: -150%;
    opacity: 0;

    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: linear-gradient(
      90deg,
      rgb(255, 60, 0) 0%,
      rgb(255, 145, 0) 100%
    );
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    padding-left: 0;
    border-bottom-right-radius: 2rem;
    /* border-top-right-radius: 2rem; */
  }

  .nav-links {
    padding: 0.8rem 1rem;
    width: 50%;
    margin: 0 20%;
    display: table;
    height: max-content;
  }
  .nav-menu:first-of-type {
    padding-top: 1rem;
  }

  .nav-links:hover {
    background: #fff;
    color: #ff9100;
    border-top-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }

  .navbar {
    height: 70px;
  }
  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(0%, 43%);
    font-size: 1.5rem;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-90%, 45%);
    font-size: 2rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
