.form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  height: max-content;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.885);
  border-radius: 1.4rem;
  margin: 3rem 10rem 5rem 10rem;
  padding: 3rem;
  padding-top: 6rem;
  z-index: 10;
  transition: all 0.2s ease-in-out;
}
.form:hover {
  box-shadow: 4px 4px 4px 2px rgba(145, 70, 0, 0.75);
  transition: all 0.2s ease-in-out;
}
.form .textfield {
  padding-bottom: 2rem;
  width: 18rem;
}
.form-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background-color: bisque;
  position: relative;
  overflow: hidden;
  /* height: 60rem; */
}
.form-container h2 {
  color: #ff7f1d;
}
.form-bg {
  position: absolute;
  top: 5rem;
  left: 50vw;
}
.form Button:first-child {
  margin-bottom: 1rem;
}
