.slider-container {
  background-color: #828282;
  height: fit-content;
}
.slider-content {
  padding: 3rem;
}
.slider-paper {
  /* width: 70vw; */
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slider-header h1 {
  margin: 0;
  padding: 3rem 3rem 0rem 3rem;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}
.slider-details {
  position: absolute;
  bottom: 10%;
  left: 10%;
  width: 20rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 5;
}
.slider-details h2 {
  color: rgb(255, 135, 23);
}

.slider-img {
  /* width: 50rem; */
  position: relative;
  margin: 0 auto;
  width: 67rem;
  height: 44rem;
}
/* .MuiPaper-root {
  width: 67rem;
  margin: 0 auto;
} */
.slider-img img {
  /* width: 70vw;
  height: 90vh; */
  width: 67rem;
  height: 44rem;
  border-radius: 10px;
}

@media screen and (max-width: 960px) {
  .slider-img {
    width: 100vw;
    height: auto;
  }
  /* .MuiPaper-root {
    width: 100vw;
    height: fit-content;
  } */
  .slider-img img {
    width: 100vw;
    height: auto;
  }
}

@media screen and (max-width: 450px) {
  .slider-details {
    left: 10vw;
  }
}
